import { textBalancer } from '../utilities/text-balancer.js'
import '../utilities/focus-visible-polyfill.js'
import { basicScroll } from '../utilities/basicScroll.js'

setTimeout(() => {
  let isMobile = false
  const setIsMobile = () => isMobile = window.matchMedia('(max-width: 768px)').matches
  window.addEventListener('resize', () => { setIsMobile() }, { passive: true })
  setIsMobile()

  const initThemeSwitcher = () => {
    document.documentElement.addEventListener('dblclick', () => {
      if (document.documentElement.getAttribute('theme') === 'dark') {
        document.documentElement.setAttribute('theme', 'light')
      } else {
        document.documentElement.setAttribute('theme', 'dark')
      }
    })
  }

  const initBalanceText = () => {
    textBalancer.initialize(Array.from(document.querySelectorAll('[data-js-balance-text]')))
  }

  const initOneDotLogosAnimated = () => {
    const animatedLogoEls = document.querySelectorAll('.OneDotLogoAnimated')

    let loaded = 0
    let position = 1050

    let bgWidth
    const image = new Image()
    image.onload = function() {
      loaded += 1
      bgWidth = this.width
      load()
    }
    image.src = 'data:image/png;base64,'

    let maskWidth
    let maskHeight
    const mask = new Image()
    mask.onload = function() {
      loaded += 1
      maskWidth = this.width
      maskHeight = this.height
      load()
    }
    mask.src = 'data:image/png;base64,'

    const load = () => {
      if (loaded < 2) return

      start()
    }

    const start = () => {
      animatedLogoEls.forEach((el) => {
        el.style.setProperty('--aspect-ratio', maskHeight / maskWidth)

        const canvas = el.querySelector('.OneDotLogoAnimated--canvas')
        const context = canvas.getContext('2d')

        const frame = () => {
          requestAnimationFrame(() => {
            if (document.scrollingElement.scrollTop < window.outerHeight) {
              position = (position + .6) % bgWidth
              draw()
            }
            frame()
          })
        }

        const draw = () => {
          canvas.width = maskWidth
          canvas.height = maskHeight

          context.drawImage(image,         - position, 0, bgWidth, maskHeight)
          context.drawImage(image, bgWidth - position, 0, bgWidth, maskHeight)

          context.globalCompositeOperation = 'xor'
          context.drawImage(mask, 0, 0, maskWidth, maskHeight)
        }

        frame()
        draw()
      })
    }
  }

  const initRemovePlayStoreLinksInIOS = () => {
    if (document.documentElement.getAttribute('is-ios') !== '') return
    document.querySelectorAll('.Button-google-play-store').forEach((el) => {
      el.parentNode.removeChild(el)
    })
  }

  const initHeroScrollIndicator = () => {
    const elem = document.querySelector('.HeroSection--scroll-indicator')

    basicScroll.create({
      elem,
      from: 'top-bottom',
      to: 'top-middle',
      direct: true,
      props: {
        opacity: {
          from: 1,
          to: 0
        }
      }
    }).start()
  }

  const initDepthPerspectiveRoots = () => {
    class DepthEl {
      constructor(el) {
        this.el = el
        this.depth = parseInt(el.getAttribute('data-depth'), 10)
        this.translateY = 0
        this.targetTranslateY = 0
        this.wasDrawnOnce = false
      }

      normalize(value) {
        return Math.round(value * 100) / 100
      }

      draw() {
        if (this.translateY === this.lastDrawnTranslateY) return
        this.el.style.transform = `translate3d(0, ${ this.translateY }rem, 0)`
        this.lastDrawnTranslateY = this.translateY
        if (this.progressWasEverSet) this.wasEverDrawn = true
      }

      update() {
        if (this.wasEverDrawn) {
          this.translateY = this.normalize(.9 * this.translateY + .1 * this.targetTranslateY)
        } else {
          this.translateY = this.targetTranslateY
        }
        this.draw()
      }

      setTargetFromProgress(progress) {
        this.progressWasEverSet = true
        const depth = isMobile ? 2 : this.depth
        const progressCenter = isMobile ? 100 : 50
        this.targetTranslateY = this.normalize(65 * ((progressCenter - progress) / 100) * (1 / (depth * 3)))
        if (isMobile && this.depth !== 1) this.targetTranslateY = 0
      }
    }

    const depthRootEls = document.querySelectorAll('[data-depth-perspective-root]')

    depthRootEls.forEach((el) => {
      const depthElInstances = Array.from(el.querySelectorAll('[data-depth]')).map((el) => new DepthEl(el))
      const scrollEl = el.querySelector('[data-depth-perspective-scroll-anchor]')

      const update = (_, p) => {
        const progress = Math.max(0, Math.min(100, p))

        depthElInstances.forEach((depthElInstance) => {
          depthElInstance.setTargetFromProgress(progress)
        })
      }

      basicScroll.create({
        elem: scrollEl,
        from: scrollEl ? 'top-bottom': '0px',
        to: scrollEl ? 'bottom-top': '100px',
        inside: update,
        outside: update
      }).start()

      const frame = () => {
        depthElInstances.forEach((depthElInstance) => {
          depthElInstance.update()
        })

        requestAnimationFrame(frame)
      }

      frame()
    })
  }

  const initPerformanceSectionBarAnimation = () => {
    const elem = document.querySelector('.PerformanceSection--figure')
    if (elem.getAttribute('is-animation-started') === '') {
      elem.removeAttribute('is-animation-started')
    }

    let basicScrollInstance

    const update = (_, p) => {
       const progress = Math.max(0, Math.min(100, p))

       if (progress === 100) {
        basicScrollInstance.stop()

        requestAnimationFrame(() => {
          elem.setAttribute('is-animation-started', '')
        })
      }
    }

    basicScrollInstance = basicScroll.create({
      elem,
      from: '0px',
      to: '100px',
      inside: update,
      outside: update
    })

    basicScrollInstance.start()
  }

  const init = () => {
    initThemeSwitcher()
    initBalanceText()
    initOneDotLogosAnimated()
    initRemovePlayStoreLinksInIOS()
    initHeroScrollIndicator()
    initDepthPerspectiveRoots()
    initPerformanceSectionBarAnimation()
  }

  init()
}, 0)
